"use client";

import { useState } from "react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";

export function PetitionForm() {
  const [name, setName] = useState("");
  const [reason, setReason] = useState("");
  const [submitted, setSubmitted] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!name || !reason) return;

    // Store in localStorage
    const petitions = JSON.parse(localStorage.getItem("petitions") || "[]");
    petitions.push({
      id: Date.now().toString(),
      name,
      reason,
      timestamp: Date.now(),
    });
    localStorage.setItem("petitions", JSON.stringify(petitions));

    // Add to leaderboard
    const leaders = JSON.parse(localStorage.getItem("leaders") || "[]");
    const existing = leaders.find((l: any) => l.name === name);
    if (existing) {
      existing.points += 10;
      existing.lastSeen = new Date().toISOString();
    } else {
      leaders.push({
        id: Date.now().toString(),
        name,
        points: 10,
        lastSeen: new Date().toISOString(),
      });
    }
    localStorage.setItem("leaders", JSON.stringify(leaders));

    setSubmitted(true);
    setName("");
    setReason("");

    setTimeout(() => setSubmitted(false), 3000);
  };

  return (
    <section id="petition" className="py-20 px-4 bg-slate-50">
      <div className="max-w-2xl mx-auto">
        <h2 className="font-display text-5xl text-center mb-4 text-cavs-wine">
          SIGN THE PETITION
        </h2>
        <p className="text-center text-slate-600 mb-12">
          Add your voice to the Witness Protection Program
        </p>

        <Card>
          <CardHeader>
            <CardTitle>Declare Your Allegiance</CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label
                  htmlFor="name"
                  className="block text-sm font-medium mb-2"
                >
                  Your Name
                </label>
                <Input
                  id="name"
                  type="text"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  placeholder="Enter your name"
                  required
                />
              </div>

              <div>
                <label
                  htmlFor="reason"
                  className="block text-sm font-medium mb-2"
                >
                  Why LeBron is the GOAT
                </label>
                <Textarea
                  id="reason"
                  value={reason}
                  onChange={(e) => setReason(e.target.value)}
                  placeholder="Share your testimony..."
                  rows={4}
                  required
                />
              </div>

              <Button type="submit" className="w-full" size="lg">
                {submitted ? "Signed! 👑" : "Sign the Petition"}
              </Button>

              {submitted && (
                <p className="text-center text-sm text-cavs-wine font-semibold">
                  Welcome to the Witness Protection Program! +10 Glaze Points
                </p>
              )}
            </form>
          </CardContent>
        </Card>
      </div>
    </section>
  );
}
